
import { Options, Vue } from 'vue-class-component';
import TableStore from "@/store/TableStore";
import { RoundInfo,PlayerInfo,MovePoint,Liquidation }  from "@/@types/type";
@Options({
  props: {
    finish: Number,
    winner: Number,
    parent: Number,

  },
})
export default class TumoLiquidation extends Vue {
    winner!:number;
    parent!:number;
    private parentPay:number = 0;
    private childPay:number = 0;
    private movePoint:MovePoint[] = [];
    get roundInfo():RoundInfo {
      return TableStore.getRoundInfo;
    }
    get tumoResult():string {
      let movePoint:MovePoint[] = [];
      let result:string = "";
      let parent:string = "";
      let child:string[] = [];
      let totalParentPay:(pay:number)=>number= ((pay:number)=>{
        return pay+ this.roundInfo.keepParent * 100
      })
      if(this.winner === -1) {
        return "";
      } 
      //点を払う親、子を判定
      TableStore.getPlayerInfo.forEach((info:PlayerInfo)=>{
        if(this.winner === info.id) {
          movePoint.push({id:info.id,point:0});
        }else if(info.id === this.parent) {
          parent = info.name;
          movePoint.push({id:info.id,point:(totalParentPay(this.parentPay)*-1)});
        }else{
          child.push(info.name);
          movePoint.push({id:info.id,point:(totalParentPay(this.childPay)*-1)});
        }
      });
      this.movePoint = movePoint;
      const childStr:string = child.join(",") + " => -" + (totalParentPay(this.childPay));
      if(this.parent === this.winner) {
        result = childStr
      } else {
        result = parent +  " => -" + (totalParentPay(this.parentPay))+"\n" + childStr;
      }
      return result;
    }

    movePointFunc():Liquidation {
      const keepParent:boolean = this.winner === this.parent;
      const totalPlus:number =-1*(this.movePoint.reduce((sum, i) => sum + i.point, 0)+this.roundInfo.point);
      const movePointList:MovePoint[] =  this.movePoint.map((row:MovePoint)=>{
        row.point = row.id === this.winner ? totalPlus:row.point;
        return row;
      });
      return {keepParentFlg:keepParent,movePointList:movePointList};
    }
}
