
import { Options, Vue } from 'vue-class-component';
import PointDisplay from "@/components/PointDisplay.vue";
import Player from "@/components/Player.vue";
import RoundInfo from "@/components/RoundInfoDisplay.vue";
import { PlayerInfo,TableInfo }  from "@/@types/type";
import TableStore from "@/store/TableStore";
@Options({
  components:{
    PointDisplay,
    Player,
    RoundInfo
  },
})

export default class CenterTable extends Vue {
    get tableInfo():TableInfo {
      return TableStore.getTableInfo;
    }

    get playerInfo():PlayerInfo[] {
      return this.tableInfo.playerInfoList;
    }
}

