
import { Options, Vue } from 'vue-class-component';
import PointDisplay from "@/components/PointDisplay.vue";
import Wind from "@/components/Wind.vue";
import {PlayerInfo} from "@/@types/type";
import TableStore from "@/store/TableStore";


@Options({           
  components:{
    PointDisplay,
    Wind
  },
  props:{
    playerInfo: {
      type:Object,
      default:{wind:"",reach:false,point:25000}
    }
  }
})

export default class PointDisp extends Vue {
  playerInfo!:PlayerInfo
  private doReach(id:number) {
    TableStore.toggleReach(id)
  }
}

